const statusColorMap = {
  active: "success",
  paused: "danger",
  vacation: "warning",
};

const INITIAL_VISIBLE_COLUMNS = ["name", "role", "status", "actions"];

export function CustomTable() {
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const { setAlert } = useAlertStore();

  const [filterValue, setFilterValue] = useState("");
  const [selectedKeys, setSelectedKeys] = useState(new Set([]));
  const [visibleColumns, setVisibleColumns] = useState(
    new Set(INITIAL_VISIBLE_COLUMNS)
  );
  const [statusFilter, setStatusFilter] = useState("all");
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [sortDescriptor, setSortDescriptor] = useState<SortDescriptor>({
    column: "age",
    direction: "ascending",
  });
  const [page, setPage] = useState(1);

  const pages = Math.ceil(users.length / rowsPerPage);

  const hasSearchFilter = Boolean(filterValue);

  const headerColumns = useMemo(() => {
    if (visibleColumns === "all") return columns;

    return columns.filter((column) =>
      Array.from(visibleColumns).includes(column.uid)
    );
  }, [visibleColumns]);

  const filteredItems = useMemo(() => {
    let filteredUsers = [...users];

    if (hasSearchFilter) {
      filteredUsers = filteredUsers.filter((user) =>
        user.name.toLowerCase().includes(filterValue.toLowerCase())
      );
    }
    if (
      statusFilter !== "all" &&
      Array.from(statusFilter).length !== statusOptions.length
    ) {
      filteredUsers = filteredUsers.filter((user) =>
        Array.from(statusFilter).includes(user.status)
      );
    }

    return filteredUsers;
  }, [users, filterValue, statusFilter]);

  const items = useMemo(() => {
    const start = (page - 1) * rowsPerPage;
    const end = start + rowsPerPage;

    return filteredItems.slice(start, end);
  }, [page, filteredItems, rowsPerPage]);

  const sortedItems = useMemo(() => {
    return [...items].sort((a, b) => {
      const first = a[sortDescriptor.column];
      const second = b[sortDescriptor.column];
      const cmp = first < second ? -1 : first > second ? 1 : 0;

      return sortDescriptor.direction === "descending" ? -cmp : cmp;
    });
  }, [sortDescriptor, items]);

  const onRowsPerPageChange = useCallback((e) => {
    setRowsPerPage(Number(e.target.value));
    setPage(1);
  }, []);

  const onSearchChange = useCallback((value) => {
    if (value) {
      setFilterValue(value);
      setPage(1);
    } else {
      setFilterValue("");
    }
  }, []);
  const t = {
    id: 1,
    first_name: "Shakhriyor",
    last_name: "Mamadaliev",
    email: "admin@gmail.com",
    picture: {
      portrait: null,
    },
    created_at: "2025-12-01T11:13:13.071484+05:00",
    last_password_update: null,
    regular_auth: true,
    is_active: true,
    is_blocked: false,
  };
  const getData = useCallback(async () => {
    try {
      const { data } = await getRequest({ url: ENDPOINTS.user_list });
      setData(data);
    } catch (err) {
      const { data, status } = getError(err as AxiosErrorProps);
      if (status && status >= 500) {
        setAlert((prev) => ({
          ...prev,
          isVisible: true,
          color: "danger",
          description: "Internal server error.",
        }));
      } else {
        setAlert((prev) => ({
          ...prev,
          isVisible: true,
          color: "danger",
          description: data?.message,
        }));
      }
    } finally {
      setIsLoading(true);
    }
  }, []);

  useEffect(() => {
    getData();
  }, []);

  console.log(data);

  return (
    <Table
      isCompact
      removeWrapper
      aria-label="Example table with custom cells, pagination and sorting"
      bottomContentPlacement="outside"
      checkboxesProps={{
        size: "sm",
        classNames: {
          wrapper: "after:bg-foreground after:text-background text-background",
        },
      }}
      classNames={{
        wrapper: ["max-h-[382px]", "max-w-3xl"],
        th: [
          "bg-transparent",
          "text-default-500",
          "border-b",
          "border-divider",
        ],
        td: [
          "first:group-data-[first=true]/tr:before:rounded-none",
          "last:group-data-[first=true]/tr:before:rounded-none",
          "group-data-[middle=true]/tr:before:rounded-none",
          "first:group-data-[last=true]/tr:before:rounded-none",
          "last:group-data-[last=true]/tr:before:rounded-none",
        ],
      }}
      selectedKeys={selectedKeys}
      selectionMode="multiple"
      sortDescriptor={sortDescriptor}
      // topContent={
      //   <TopToolbar
      //     visibleColumns={visibleColumns}
      //     setPage={setPage}
      //     setRowsPerPage={setRowsPerPage}
      //     columns={columns}
      //   />
      // }
      // bottomContent={
      //   <BottomToolbar
      //     pages={pages}
      //     setPage={setPage}
      //     selectedKeys={selectedKeys}
      //     items={data}
      //   />
      // }
      topContentPlacement="outside"
      onSelectionChange={setSelectedKeys}
      onSortChange={setSortDescriptor}
    >
      <TableHeader columns={columns}>
        {(column) => (
          <TableColumn
            key={column.uid}
            align={column.uid === "actions" ? "end" : "start"}
            allowsSorting={column.sortable}
          >
            {column.name}
          </TableColumn>
        )}
      </TableHeader>
      <TableBody
        emptyContent={"No users found"}
        items={data}
        isLoading={isLoading}
      >
        {(item) => {
          console.log(item);

          return (
            <TableRow key={item.id}>
              <TableCell>
                <User
                  avatarProps={{
                    radius: "full",
                    size: "sm",
                    src: item.picture.portrait,
                  }}
                  classNames={{
                    description: "text-default-500",
                  }}
                  description={item.email}
                  name={`${item.first_name} ${item.last_name}`}
                >
                  {item.email}
                </User>
              </TableCell>
              <TableCell>{item.role}</TableCell>
              <TableCell>
                <Chip
                  className="capitalize border-none gap-1 text-default-600"
                  color={"success"}
                  size="sm"
                  variant="dot"
                >
                  Active
                </Chip>
              </TableCell>
              <TableCell>
                <Chip
                  className="capitalize border-none gap-1 text-default-600"
                  color={"success"}
                  size="sm"
                  variant="dot"
                >
                  Active
                </Chip>
              </TableCell>
              <TableCell>created at</TableCell>
              <TableCell> {longDate(item.created_at)}</TableCell>
              <TableCell>
                <Dropdown className="bg-background border-1 border-default-200">
                  <DropdownTrigger>
                    <Button isIconOnly radius="full" size="sm" variant="light">
                      <PiDotsThreeOutlineVerticalFill className="text-default-500 w-4 h-4" />
                    </Button>
                  </DropdownTrigger>
                  <DropdownMenu>
                    <DropdownItem key="view">View</DropdownItem>
                    <DropdownItem key="edit">Edit</DropdownItem>
                    <DropdownItem key="delete">Delete</DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </TableCell>
            </TableRow>
          );
        }}
      </TableBody>
    </Table>
  );
}
